import { Component } from '@angular/core';
import {
  IonicPage,
  NavController,
  NavParams,
  Loading,
  LoadingController,
  ModalController,
  Modal,
} from 'ionic-angular';
import { HomePage } from '../home/home';
import { AnalisisProvider } from '../../providers/analisis/analisis';
import { Response } from '@angular/http';

/**
 * Generated class for the ResultadosPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name: 'ResultadosPage',
})
@Component({
  selector: 'page-resultados',
  templateUrl: 'resultados.html',
})
export class ResultadosPage {
  id: string = '0';
  url: string = '';
  loading: Loading;

  modal: Modal;
  resultado: any;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private analisisPrv: AnalisisProvider,
    private loadingCtrl: LoadingController,
    private modalCtrl: ModalController
  ) {
    if (this.navParams.get('id')) {
      this.id = this.navParams.get('id');
      this.url = this.navParams.get('url');
    } else {
      this.navCtrl.setRoot(HomePage);
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ResultadosPage');

    if (this.id !== '0') {
      this.buscarResultados();
    }
  }

  showLoader() {
    this.loading = this.loadingCtrl.create({
      content: 'Recuperando resultados...',
    });
    this.loading.present();
  }

  buscarResultados() {
    this.showLoader();
    this.analisisPrv.analizarUrl(Number(this.id)).subscribe(
      (data: Response) => {
        this.loading.dismiss();
        console.log(data.json());
        this.resultado = data.json();
      },
      (error) => {
        this.loading.dismiss();
      }
    );
  }

  calcularResultado(): string {
    if (this.resultado) {
      let resultado =
        Number(this.resultado.knn) +
        Number(this.resultado.naive) +
        Number(this.resultado.svc);

      if (resultado > 1) {
        return 'La url ingresada es inofensiva.';
      } else {
        return 'La url ingresada es potencialmente peligrosa';
      }
    }
  }

  verDetalle() {
    this.modal = this.modalCtrl.create('ResultadosDetallePage', {
      resultados: this.resultado,
      id: this.id,
      url: this.url,
    });
    this.modal.present();
  }
}



